:root {
  --color-white: #ffffff;
  --color-black: #000000;
  --text-black: #22252F;
  --border-gray: #2D333C;
  --gray-400: #E2E3EA;
  --text-gray-light: #7F808A;
  --text-gray: #8E8F9E;
  --bg-soft: #efefef;
  --bg-blue: #e0f2f7;
  --bg-green: #e6ffe6;
  --bg-red: #ffe6e6;
  --bg-hover: #f9f9f9;
  --color-green: #008000;
  --color-red: #eb3423;
}

.bc-container {
  max-width: 1656px;
  padding-left: 16px;
  padding-right: 16px;
  margin: 0 auto;
}

.barrier-configurator h1,
.barrier-configurator h2,
.barrier-configurator h3,
.barrier-configurator h4,
.barrier-configurator h5,
.barrier-configurator h6 {
  color: var(--text-black);
  margin: 0;
}

.barrier-configurator .h1, .barrier-configurator .bc__head--h1 {
  font-size: 70px;
  font-size: clamp(32px, 5vw, 70px);
  font-weight: 700;
  line-height: 120%;
  margin-top: 0;
  margin-bottom: 16px;
}

.barrier-configurator .h2, .barrier-configurator .bc__head--h2 {
  font-size: clamp(24px, 5vw, 44px);
  font-style: normal;
  font-weight: 700;
  line-height: 120%;
  margin-bottom: 20px;
}

.barrier-configurator .bc__head--subtitle {
  color: var(--text-gray-light);
  font-size: 60px;
  font-size: clamp(28px, 4vw, 60px);
  font-weight: 300;
  line-height: 110%;
}

.barrier-configurator ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

.barrier-configurator .bc-content {
  margin: 0 auto;
  padding: 24px 16px 60px;
  background-color: #E2E3EA;
  border-radius: 20px;
}

.barrier-configurator .bc__section {
  margin-bottom: 84px;
}

.barrier-configurator .bc__section-wrapper {
  margin-left: 52px;
  margin-right: 8px;
}

.barrier-configurator .bc__section-title {
  display: flex;
  font-size: 40px;
  line-height: 40px;
  font-weight: 700;
  margin-bottom: 32px;
  margin-left: 4px;
}

.barrier-configurator .bc__section-title span {
  font-size: 32px;
  margin-left: 32px;
}

.barrier-configurator .bc__section-title-small {
  font-size: 16px;
  font-weight: 400;
  margin-bottom: 10px;
  color: var(--text-gray-light);
}

.barrier-configurator .bc__section-title-xs-small {
  font-size: 12px;
  font-weight: 600;
  line-height: 16px;
  margin-bottom: 16px;
  color: var(--text-dark);
}

@media screen and (min-width: 1367px) {
  .barrier-configurator .bc-content {
    padding: 60px 60px 100px 60px;
    border-radius: 45px;
  }
}

@media screen and (min-width: 902px) {
  .barrier-configurator .bc-content {
    padding: 60px 48px 100px;
  }
}

@media screen and (min-width: 601px) {
  .barrier-configurator .bc-container {
    width: 90%;
    padding-left: 0;
    padding-right: 0;
  }
  .barrier-configurator .bc-content {
    padding: 48px 32px 60px;
  }
  .barrier-configurator .h1, .barrier-configurator .bc__head--h1 {
    margin-bottom: 40px;
  }
  .barrier-configurator .h2, .barrier-configurator .bc__head--h2 {
    font-size: 44px;
    margin-bottom: 52px;
  }
}
